<template>
    <div
        class="invoice-wrap flex flex-column"
        ref="invoiceWrap"
        @click="checkClick"
    >
        <form
            class="invoice-content"
            @submit.prevent="submitForm"
        >
            <loading v-show="loading" />

            <!-- Title -->
            <h1 v-if="!editInvoice">New Invoice</h1>
            <h1 v-else>Edit Invoice</h1>

            <!-- Bill From -->
            <div class="bill-from flex flex-column">
                <h4>Bill From</h4>
                <div class="input flex flex-column">
                    <label for="billerStreetAddress">Street Address</label>
                    <input required type="text" id="billerStreetAddress" v-model="billerStreetAddress" />
                </div>
                <div class="location-details flex">
                    <div class="input flex flex-column">
                        <label for="billerCity">City</label>
                        <input required type="text" id="billerCity" v-model="billerCity" />
                    </div>
                    <div class="input flex flex-column">
                        <label for="billerZipCode">Zip Code</label>
                        <input required type="text" id="billerZipCode" v-model="billerZipCode" />
                    </div>
                    <div class="input flex flex-column">
                        <label for="billerCountry">Country</label>
                        <input required type="text" id="billerCountry" v-model="billerCountry" />
                    </div>
                </div>
            </div>

            <!-- Bill To -->
            <div class="bill-to flex flex-column">
                <h4>Bill To</h4>
                <div class="input flex flex-column">
                    <label for="clientName">Client's Name</label>
                    <input required type="text" id="clientName" v-model="clientName" />
                </div>
                <div class="input flex flex-column">
                    <label for="clientEmail">Client's Email</label>
                    <input required type="text" id="clientEmail" v-model="clientEmail" />
                </div>
                <div class="input flex flex-column">
                    <label for="clientStreetAddress">Street Address</label>
                    <input required type="text" id="clientStreetAddress" v-model="clientStreetAddress" />
                </div>
                <div class="location-details flex">
                    <div class="input flex flex-column">
                        <label for="clientCity">City</label>
                        <input required type="text" id="clientCity" v-model="clientCity" />
                    </div>
                    <div class="input flex flex-column">
                        <label for="clientZipCode">Zip Code</label>
                        <input required type="text" id="clientZipCode" v-model="clientZipCode" />
                    </div>
                    <div class="input flex flex-column">
                        <label for="clientCountry">Country</label>
                        <input required type="text" id="clientCountry" v-model="clientCountry" />
                    </div>
                </div>
            </div>

            <!-- Invoice Work Details -->
            <div class="invoice-work flex flex-column">
                <div class="payment flex">
                    <div class="input flex flex-column">
                        <label for="invoiceDate">Invoice Date</label>
                        <input disabled type="text" id="invoiceDate" v-model="invoiceDate" />
                    </div>
                    <div class="input flex flex-column">
                        <label for="paymentDueDate">Payment Due</label>
                        <input disabled type="text" id="paymentDueDate" v-model="paymentDueDate" />
                    </div>
                </div>
                <div class="input flex flex-column">
                    <label for="paymentTerms">Payment Terms</label>
                    <select required type="text" id="paymentTerms" v-model="paymentTerms">
                        <option value="30">Net 30 Days</option>
                        <option value="60">Net 60 Days</option>
                    </select>
                </div>
                <div class="input flex flex-column">
                    <label for="productDescription">Product Description</label>
                    <input required type="text" id="productDescription" v-model="productDescription" />
                </div>
                <div class="work-items">
                    <h3>Item List</h3>
                    <table class="item-list">
                        <tr class="table-heading flex">
                            <th class="item-name">Item Name</th>
                            <th class="qty">Qty</th>
                            <th class="price">Price</th>
                            <th class="total">Toal</th>
                        </tr>
                        <tr
                            class="table-items flex"
                            v-for="(item, index) in invoiceItemList"
                            :key="index"
                        >
                            <td class="item-name">
                                <input type="text" v-model="item.itemName" />
                            </td>
                            <td class="qty">
                                <input type="text" v-model="item.qty" />
                            </td>
                            <td class="price">
                                <input type="text" v-model="item.price" />
                            </td>
                            <td class="total flex">
                                ${{ (item.total = item.qty * item.price) }}
                            </td>
                            <img @click="deleteInvoiceItem(item.id)" src="@/assets/icon-delete.svg" alt="" />
                        </tr>
                    </table>

                    <div @click="addNewInvoiceItem" class="flex button">
                        <img src="@/assets/icon-plus.svg" alt="" />
                        Add New Item
                    </div>
                </div>
            </div>

            <!-- Save/Exit -->
            <div class="save flex">
                <div class="left">
                    <button
                        type="button"
                        class="red"
                        @click="closeInvoice"
                    >Cancel</button>
                </div>
                <div class="right flex">
                    <button
                        v-if="!editInvoice"
                        type="submit"
                        class="dark-purple"
                        @click="saveDraft"
                    >Save Draft</button>
                    <button
                        v-if="!editInvoice"
                        type="submit"
                        class="purple"
                        @click="publishInvoice"
                    >Create Invoice</button>
                    <button
                        v-if="editInvoice"
                        type="sumbit"
                        class="purple"
                    >Update Invoice</button>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import { db } from '../firebase/firebaseInit'
import { collection, doc, addDoc, updateDoc } from 'firebase/firestore'
import { mapActions, mapMutations, mapState } from 'vuex'
import { uid } from 'uid'
import Loading from './Loading'

export default {
    name: 'invoice-modal',
    components: {
        Loading
    },
    data () {
        return {
            loading: null,
            docId: null,
            dateOptions: {
                year: "numeric",
                month: "short",
                day: "numeric"
            },
            billerStreetAddress: null,
            billerCity: null,
            billerZipCode: null,
            billerCountry: null,
            clientName: null,
            clientEmail: null,
            clientStreetAddress: null,
            clientCity: null,
            clientZipCode: null,
            clientCountry: null,
            invoiceDateUnix: null,
            invoiceDate: null,
            paymentTerms: null,
            paymentDueDateUnix: null,
            paymentDueDate: null,
            productDescription: null,
            invoicePending: null,
            invoiceDraft: null,
            invoiceItemList: [],
            invoiceTotal: 0
        }
    },
    created () {
        if (!this.editInvoice) {
            this.invoiceDateUnix = Date.now()
            this.invoiceDate = new Date(this.invoiceDateUnix).toLocaleString('en-us', this.dateOptions)
        }

        if (this.editInvoice) {
            const currentInvoice = this.currentInvoiceArray[0]

            this.docId = currentInvoice.docId
            this.billerStreetAddress = currentInvoice.billerStreetAddress
            this.billerCity = currentInvoice.billerCity
            this.billerZipCode = currentInvoice.billerZipCode
            this.billerCountry = currentInvoice.billerCountry
            this.clientName = currentInvoice.clientName
            this.clientEmail = currentInvoice.clientEmail
            this.clientStreetAddress = currentInvoice.clientStreetAddress
            this.clientCity = currentInvoice.clientCity
            this.clientZipCode = currentInvoice.clientZipCode
            this.clientCountry = currentInvoice.clientCountry
            this.invoiceDateUnix = currentInvoice.invoiceDateUnix
            this.invoiceDate = currentInvoice.invoiceDate
            this.paymentTerms = currentInvoice.paymentTerms
            this.paymentDueDateUnix = currentInvoice.paymentDueDateUnix
            this.paymentDueDate = currentInvoice.paymentDueDate
            this.productDescription = currentInvoice.productDescription
            this.invoicePending = currentInvoice.invoicePending
            this.invoiceDraft = currentInvoice.invoiceDraft
            this.invoiceItemList = currentInvoice.invoiceItemList
            this.invoiceTotal = currentInvoice.invoiceTotal
        }
    },
    methods: {
        ...mapMutations(['TOGGLE_INVOICE', 'TOGGLE_MODAL', 'TOGGLE_EDIT_INVOICE']),
        ...mapActions(['GET_INVOICES' ,'UPDATE_INVOICE']),
        checkClick (e) {
            if (e.target === this.$refs.invoiceWrap) {
                this.TOGGLE_MODAL()
            }
        },
        closeInvoice () {
            this.TOGGLE_INVOICE()
            if (this.editInvoice) this.TOGGLE_EDIT_INVOICE()
        },
        addNewInvoiceItem () {
            this.invoiceItemList.push({
                id: uid(),
                itemName: '',
                qty: '',
                price: 0,
                total: 0
            })
        },
        deleteInvoiceItem (id) {
            this.invoiceItemList = this.invoiceItemList.filter(item => item.id !== id)
        },
        calInvoiceTotal () {
            this.invoiceTotal = 0
            this.invoiceItemList.forEach(item => {
                this.invoiceTotal += item.total
            })
        },
        publishInvoice () {
            this.invoicePending = true
        },
        saveDraft () {
            this.invoiceDraft = true
        },
        async uploadInvoice () {
            if (this.invoiceItemList.length <= 0) {
                alert('Please ensure you filled out work items!')
                return
            }

            this.calInvoiceTotal()

            this.loading = true

            try {
                const docRef = await addDoc(collection(db, 'invoices'), {
                    invoiceId: uid(6),
                    billerStreetAddress: this.billerStreetAddress,
                    billerCity: this.billerCity,
                    billerZipCode: this.billerZipCode,
                    billerCountry: this.billerCountry,
                    clientName: this.clientName,
                    clientEmail: this.clientEmail,
                    clientStreetAddress: this.clientStreetAddress,
                    clientCity: this.clientCity,
                    clientZipCode: this.clientZipCode,
                    clientCountry: this.clientCountry,
                    invoiceDate: this.invoiceDate,
                    invoiceDateUnix: this.invoiceDateUnix,
                    paymentTerms: this.paymentTerms,
                    paymentDueDate: this.paymentDueDate,
                    paymentDueDateUnix: this.paymentDueDateUnix,
                    productDescription: this.productDescription,
                    invoiceItemList: this.invoiceItemList,
                    invoiceTotal: this.invoiceTotal,
                    invoicePending: this.invoicePending,
                    invoiceDraft: this.invoiceDraft,
                    invoicePaid: null,
                })
                console.log("Document written with ID: ", docRef.id)
            } catch (e) {
                console.error("Error adding document: ", e)
            }

            this.loading = false

            this.TOGGLE_INVOICE()

            this.GET_INVOICES()
        },
        async updateInvoice () {
            if (this.invoiceItemList.length <= 0) {
                alert('Please ensure you filled out work items!')
                return
            }

            this.calInvoiceTotal()

            this.loading = true

            try {
                const docIdRef = doc(db, "invoices", this.docId);

                await updateDoc(docIdRef, {
                    billerStreetAddress: this.billerStreetAddress,
                    billerCity: this.billerCity,
                    billerZipCode: this.billerZipCode,
                    billerCountry: this.billerCountry,
                    clientName: this.clientName,
                    clientEmail: this.clientEmail,
                    clientStreetAddress: this.clientStreetAddress,
                    clientCity: this.clientCity,
                    clientZipCode: this.clientZipCode,
                    clientCountry: this.clientCountry,
                    paymentTerms: this.paymentTerms,
                    paymentDueDate: this.paymentDueDate,
                    paymentDueDateUnix: this.paymentDueDateUnix,
                    productDescription: this.productDescription,
                    invoiceItemList: this.invoiceItemList,
                    invoiceTotal: this.invoiceTotal
                })

                console.log("Document updated with ID: ", docIdRef.id)
            } catch (e) {
                console.error("Error updating document: ", e)
            }

            this.loading = false

            const data = {
                docId: this.docId,
                routeId: this.$route.params.invoiceId
            }

            this.UPDATE_INVOICE(data)
        },
        submitForm () {
            if (this.editInvoice) {
                this.updateInvoice()
                return
            }

            this.uploadInvoice()
        }
    },
    computed: {
        ...mapState(['editInvoice', 'currentInvoiceArray']),
    },
    watch: {
        paymentTerms () {
            const futureDate = new Date()
            this.paymentDueDateUnix = futureDate.setDate(futureDate.getDate() + parseInt(this.paymentTerms))
            this.paymentDueDate = new Date(this.paymentDueDateUnix).toLocaleString('en-us', this.dateOptions)
        }
    }
}
</script>

